html, body {
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 1.5;
  font-family: OpenSans;
  color: #000;
  background-color: #B9C3D1; }

.outer-wrapper {
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-image: url(../images/bg-front.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover; }

.inner-wrapper {
  background: linear-gradient(black 0%, rgba(255, 0, 0, 0) 4%); }

.content-wrapper {
  overflow: hidden; }

.container {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 1170px; }
  .container:before, .container:after {
    content: " ";
    display: table; }
