.b-news {
  padding: 15px;
  background: #FFF;
  border: 1px solid #d8d8d8;
  border-top: none; }
  .b-news:hover {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3); }
  .b-news .b-news__view {
    height: 180px;
    overflow: hidden; }
    .b-news .b-news__view img {
      width: 100%;
      height: auto; }
  .b-news__text {
    display: flex;
    flex-direction: column;
    height: 100%; }
  .b-news__title {
    margin: 16px 0 10px;
    font-family: Montserrat;
    font-weight: bold;
    font-size: 21px;
    line-height: 1.3; }
    .b-news__title a {
      text-decoration: none;
      color: #000; }
  .b-news__description p {
    margin-top: 0; }
  .b-news__info {
    margin-top: 37px;
    font-size: 10px;
    color: #666; }
    .b-news__info span {
      margin-right: 10px; }
  .b-news__info .b-news__watches {
    float: right;
    margin-right: 0;
    line-height: 1; }
    .b-news__info .b-news__watches svg {
      margin: -2px 4px 0 0;
      vertical-align: middle; }
  .b-news__likes {
    line-height: 1;
    color: #016DAE;
    cursor: pointer; }
    .b-news__likes svg {
      margin-top: -2px;
      vertical-align: middle; }
    .b-news__likes path {
      fill: transparent;
      stroke: #1f84c8; }
