.btn-wrap {
  margin: 28px 0 100px;
  text-align: center; }

.section.s-news {
  margin-top: 40px; }

.owl-carousel__news .owl-nav .owl-prev.disabled:before, .owl-carousel__news .owl-nav .owl-next.disabled:before,
.owl-carousel__videos .owl-nav .owl-prev.disabled:before,
.owl-carousel__videos .owl-nav .owl-next.disabled:before,
.owl-carousel__sponsors .owl-nav .owl-prev.disabled:before,
.owl-carousel__sponsors .owl-nav .owl-next.disabled:before {
  display: none; }

.owl-carousel__news .owl-nav .owl-prev span, .owl-carousel__news .owl-nav .owl-next span,
.owl-carousel__videos .owl-nav .owl-prev span,
.owl-carousel__videos .owl-nav .owl-next span,
.owl-carousel__sponsors .owl-nav .owl-prev span,
.owl-carousel__sponsors .owl-nav .owl-next span {
  display: none; }

.owl-carousel__news .owl-nav .owl-prev:before, .owl-carousel__news .owl-nav .owl-next:before,
.owl-carousel__videos .owl-nav .owl-prev:before,
.owl-carousel__videos .owl-nav .owl-next:before,
.owl-carousel__sponsors .owl-nav .owl-prev:before,
.owl-carousel__sponsors .owl-nav .owl-next:before {
  z-index: 20;
  content: '';
  display: block;
  width: 20px;
  height: 26px;
  cursor: pointer;
  top: 30%;
  left: -3%;
  position: absolute;
  background: url(../images/arrow.svg) center center no-repeat; }

.owl-carousel__news .owl-nav .owl-next:before,
.owl-carousel__videos .owl-nav .owl-next:before,
.owl-carousel__sponsors .owl-nav .owl-next:before {
  left: auto;
  right: -3%;
  transform: rotate(180deg); }

.s-video .video-item-wrap {
  background: #FFF; }

.sponsors-item-wrap {
  background-color: white;
  margin-bottom: 100px; }

.gallery-item-wrap {
  height: 725px;
  background-color: white; }
