.menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: auto;
  margin: 0;
  padding: 0;
  border: none;
  list-style: none; }
  .menu > li {
    display: inline-block;
    max-width: 155px;
    text-align: center;
    vertical-align: middle;
    text-transform: uppercase;
    font-size: 13px; }
    .menu > li:hover > ul {
      display: block; }
    .menu > li > a {
      display: inline-flex;
      align-items: center;
      padding: 0 10px;
      height: 46px;
      color: #000;
      text-decoration: none; }
      .menu > li > a:hover {
        text-decoration: underline; }
    .menu > li > ul {
      position: absolute;
      top: 100%;
      margin: 0;
      padding: 0;
      min-width: 180px;
      border: none;
      list-style: none;
      z-index: 20;
      text-align: left;
      background-color: #c4c4c4;
      display: none; }
      .menu > li > ul > li {
        display: block;
