.b-video {
  padding: 15px;
  background: #FFF; }
  .b-video__view {
    position: relative;
    display: block;
    height: 300px;
    overflow: hidden;
    margin-bottom: 16px; }
    .b-video__view:hover {
      -webkit-filter: brightness(70%);
      -webkit-transition: all 1s ease;
      -moz-transition: all 1s ease;
      -o-transition: all 1s ease;
      -ms-transition: all 1s ease;
      transition: all 1s ease; }
    .b-video__view img {
      width: 100%;
      height: auto; }
    .b-video__view::before {
      content: "";
      position: absolute;
      top: 46%;
      left: 50%;
      -webkit-transform: translate(-50%, 0);
      transform: translate(-50%, 0);
      width: 40px;
      height: 40px;
      border-radius: 50%;
      border: 1px solid #fff;
      background: rgba(0, 0, 0, 0.2); }
    .b-video__view::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, 0);
      transform: translate(-50%, 0);
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
      border-left: 12px solid #fff;
      margin-left: 1px; }
  .b-video__info {
    margin-top: 37px;
    font-size: 10px;
    color: #666; }
    .b-video__info span {
      margin-right: 10px; }
  .b-video__watches {
    float: right; }
  .b-video__likes {
    line-height: 1;
    color: #016DAE;
    cursor: pointer; }
    .b-video__likes svg {
      margin-top: -2px;
      vertical-align: middle; }
    .b-video__likes path {
      fill: transparent;
      stroke: #1f84c8; }
